import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
            page: 1,
            list: [],
            haveMore: true,
            ReportImageType: [],
            info: this.$store.state.patient,
            show: false,
            cards:['普通病历模板','喉癌病历模板'],
            model: '普通病历模板',
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/PatientArchives/GetReportImageByPage',{
                    PatientId: this.info.PatientId,
                    PageIndex: this.page,
                    pageSize: 15
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        toUpdate(){
            const browser = navigator.userAgent
            if(browser.indexOf('iPhone') !== -1){
                if(this.model === '普通病历模板'){
                    this.$router.push('/update-case-copy')
                }
                else{
                    this.$router.push('/update-case-c-copy')
                }
            }
            else{
                if(this.model === '普通病历模板'){
                    this.$router.push('/update-case-normal')
                }
                else{
                    this.$router.push('/update-case-c')
                }
            }
            
        },
        goDetail(i){
        	if(i && i.ImgDetails && i.ImgDetails[0].FileType == 1){
        		let obj = {
					title : i.DiseaseDesc,
					linkUrl : i.ImgDetails[0].ImagePaths[0]
				}
				this.$bridge.callhandler('showPdfReport', JSON.stringify(obj))
        	}else{
        		this.$router.push(`/case-detail?ImageId=${i.ImageId}&MedicalTempId=${i.MedicalTempId}&PatientName=${i.PatientName}`)
        	}
        }
    }
}



